var websock = null;
let rec; //5WebSocket  rec
let isConnect = false; // 
let wsurl = "";
let userId = null;
let messageCallBack = null;
let openCallBack = null;
let hasLogin = false;

let createWebSocket = (url, id) => {
	wsurl = url;
	userId = id;
	initWebSocket();
};

let initWebSocket = () => {
	try {
		console.log("WebSocket");
		hasLogin = false;
		websock = new WebSocket(wsurl);
		websock.onmessage = function(e) {
			let sendInfo = JSON.parse(e.data)
			if (sendInfo.cmd == 0) {
				hasLogin = true;
				heartCheck.start()
				console.log('WebSocket')
				// 
				openCallBack && openCallBack();
			}
			else if(sendInfo.cmd==1){
				// 
				heartCheck.reset();
			} else {
				// 
				messageCallBack && messageCallBack(sendInfo.cmd,sendInfo.data)
			}
		}
		websock.onclose = function(e) {
			console.log('WebSocket')
			isConnect = false; //
		}
		websock.onopen = function() {
			console.log("WebSocket");
			isConnect = true;
			// 
			let loginInfo = {
				cmd: 0,
				data: {userId: userId}
			};
			websock.send(JSON.stringify(loginInfo));
			
		}

		// 
		websock.onerror = function() {
			console.log('WebSocket')
			isConnect = false; //
			reConnect();
		}
	} catch (e) {
		console.log("");
		reConnect(); //webSocket 
	}
};

//
let reConnect = () => {
	console.log("");
	if (isConnect) return; //
	rec && clearTimeout(rec);
	rec = setTimeout(function() { // 5  
		initWebSocket(wsurl);
	}, 5000);
};
//
let closeWebSocket = () => {
	websock.close();
};


//
var heartCheck = {
	timeout: 5000, // 20s
	timeoutObj: null, //
	start: function() {
		if(isConnect){
			console.log('WebSocket')
			let heartBeat = {
				cmd: 1,
				data: {
					userId: userId
				}
			};
			websock.send(JSON.stringify(heartBeat))
		}
	},

	reset: function(){
		clearTimeout(this.timeoutObj);
		this.timeoutObj = setTimeout(function() {
			heartCheck.start();
		}, this.timeout);
		
	}
};



// 
function sendMessage(agentData) {
	// console.log(globalCallback)
	if (websock.readyState === websock.OPEN) {
		// ws
		websock.send(JSON.stringify(agentData))
	} else if (websock.readyState === websock.CONNECTING) {
		//  1s
		setTimeout(function() {
			sendMessage(agentData)
		}, 1000)
	} else {
		//  1s
		setTimeout(function() {
			sendMessage(agentData)
		}, 1000)
	}
}


function onmessage(callback) {
	messageCallBack = callback;
}


function onopen(callback) {
	openCallBack = callback;
	if (hasLogin) {
		openCallBack();
	}
}


// 
export {
	createWebSocket,
	closeWebSocket,
	sendMessage,
	onmessage,
	onopen
}
